.gallery-item-card {
  display: block;
  width: 100%;
  padding: 16px 8px;
  text-align: center;
  box-shadow: 0 0 0 1px #e6e6e6;
  border-radius: 2px;
  line-height: 1;
  font-size: 12px;
  text-decoration: none; }
  @media screen and (min-width: 48em) {
    .gallery-item-card {
      padding: 32px 16px;
      font-size: 16px; } }
  .gallery-item-card:hover, .gallery-item-card:focus {
    box-shadow: 0 0 0 1px #e6e6e6, 2px 2px 0 0 #cacaca; }
    @media screen and (min-width: 64em) {
      .gallery-item-card:hover, .gallery-item-card:focus {
        box-shadow: 0 0 0 1px #e6e6e6, 3px 3px 0 0 #cacaca; } }
  .gallery-item-card__cover {
    max-width: 75rem;
    margin-left: auto;
    margin-right: auto;
    display: flex;
