    justify-content: center;
    align-items: center;
    height: 80px;
    margin-bottom: 16px; }
    @media screen and (min-width: 48em) {
      .gallery-item-card__cover {
        height: 150px; } }
    .gallery-item-card__cover__image {
      flex: 0 0 100%;
      padding-left: 0.375rem;
      padding-right: 0.375rem;
      max-width: 100%; }
      @media screen and (min-width: 48em) {
